$primary: #42C2FF;
$secondary: #85F4FF;
$white: #EFFFFD;
$gray: #9b9b9b;
$black: #222222;

* {
  margin: 0;
  font-family: "Poppins";
  color: black;
  background-color: white;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  // border-bottom: 1px solid black;
}

.navbar .nav-blog-title {
  color: $primary;
  text-decoration: none;
}

.navbar .links {
  margin-left: auto;
}

.navbar .nav-item {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar .nav-item:hover {
  color: $primary;
}

.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

.navbar .nav-item {
  position: relative;
  color: $black;
}

.navbar .nav-item::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: $primary;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.navbar .nav-item:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

.form__group {
  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
  width: 100%;
}

.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid $gray;
  outline: 0;
  font-size: 1.3rem;
  color: black;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;

  &::placeholder {
    color: transparent;
  }

  &:placeholder-shown ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 20px;
  }
}

.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: $gray;
}

.form__field:focus {
  ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: $gray;
    font-weight:700;    
  }
  padding-bottom: 6px;  
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, $primary, $secondary);
  border-image-slice: 1;
}
/* reset input */
.form__field{
  &:required,&:invalid { box-shadow:none; }
}
/* demo */
body {
  font-family: 'Poppins', sans-serif; 
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-size: 1.5rem;
}